@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
.location-input{flex:1 1 80%;font-family:"Raleway",sans-serif;height:40px;font-size:0.9em}.location-input input{width:70%;padding:10px;margin-right:10px;font-family:"Raleway",sans-serif;font-size:1em;border:3px solid lightgray;border-radius:10px;color:dimgray;outline:none}.location-input input:focus{box-shadow:0 0 1pt 1pt lightgrey}.location-input button{padding:12px;font-family:"Raleway",sans-serif;font-weight:normal;background:lightgrey;color:dimgray;font-size:1em;border:none;cursor:pointer;border-radius:10px;outline:none}.location-input button:focus{box-shadow:0 0 1pt 1pt grey}.invalid-input{color:red;margin-top:5px}.intro{display:flex;align-items:center;flex-direction:column;padding:5px}.intro p{margin:15px 0px 0px 0px}.intro ul{margin:5px}.intro h3{margin:5px 0px 20px 0px}.intro h4{margin:5px 0px 5px 0px}.card{background:white;padding:10px 40px 10px 40px;border-radius:10px;flex:1 1 15%;border:2px solid lightgray;margin:5px}.weather-cards-wrapper{display:flex;justify-content:center;width:80%;margin:0 auto;flex-wrap:wrap}.temperature-card-item{margin-right:20px}.card-item{display:flex;justify-content:center;padding:5px 0px 5px 10px;font-size:0.8em}.card-item p{margin-top:20px}.fade-in{opacity:1;-webkit-animation-name:fadeInOpacity;animation-name:fadeInOpacity;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-duration:0.1s;animation-duration:0.1s;text-align:center}@-webkit-keyframes fadeInOpacity{0%{opacity:0}100%{opacity:1}}@keyframes fadeInOpacity{0%{opacity:0}100%{opacity:1}}body{font-family:"Raleway",sans-serif;color:dimgray}header{display:flex;align-items:baseline;justify-content:center;margin-bottom:20px}.fair-days{margin-bottom:0}.title{padding:20px;flex:1 1 20%}.errors{color:red;text-align:center}@media screen and (min-width: 1300px){.weather-cards-wrapper{width:70%}}@media screen and (max-width: 900px){header{flex-direction:column;align-items:center}.location-input{text-align:center;width:100%}.title{padding-bottom:20px}.weather-cards-wrapper{width:100%}}@media screen and (max-width: 600px){body{font-size:0.8em}.location-input input{width:60%}}

